#testBtnsmain {
	box-shadow: 0 0 4px black;
	margin-left: 10px;
}

.testBtns:hover i {
	transform: scale(1.2);
}
.testBtns:nth-of-type(even) {
	background-color: rgba(20, 65, 0, 0.175);
}
.testBtns:nth-of-type(odd) {
	background-color: rgba(20, 65, 0, 0.04);
}
.testBtns:hover {
	box-shadow: 0 0 5px black;
}

#active {
	background-color: lightgreen;
}

@media only screen and (max-width: 900px) {
	#testBtnsmain {
		margin-left: 0;
		margin-bottom: 40px;
	}
}
